.banner {
    background: url("../img/outdoor-rink.webp") no-repeat center bottom;
    background-size: cover;
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sale {
    color: #ffffff;
    margin-left: auto;
    margin-right: 15%;
    text-align: center;
    text-transform: uppercase;
    text-shadow: 3px 3px 3px #333333;
}

h3 {
    font-weight: bold;
    font-size: 185px;
    line-height: 0.6;
}

h4 {
    font-weight: bold;
    font-size: 220px;
    margin: 0;
    line-height: 0.8; 
}

h2 {
    font-weight: bold;
    font-size: 20px;
}

h1 {
    font-weight: bold;
    font-size: 55px;
    line-height: 0.9;
}
@media only screen and (max-width: 1080px) {
    .banner{
        height: 100vh;
    }
}

@media only screen and (max-width: 810px) {
    .sale {
        margin: auto;
    }
    
    .banner{
        height: 80vh;
    }
}